<template>
  <div class="page-box index-box">
      <van-dropdown-menu>
        <van-dropdown-item v-model="locale" :options="languageList" @change="changeLanuage" />
      </van-dropdown-menu>
    <div class="top">

      <div class="logo flex-center">
        <img src="@/assets/images/new/logo@2x.png" alt="">
      </div>
      <div class="title">{{$t('nts.全球首家NFT早鸟投融资孵化平台')}}</div>
      <div v-if="end" class="title" style="color: #9752C7; font-size: 36px;">{{$t('nts.NTSLIST即将全球上线，敬请期待！')}}</div>
      <div class="time-box" v-if="!end">
        <div class="time-title">{{$t('nts.NftList系统参投结束倒计时')}}</div>
        <index-count-down
          :endDate="time"
          @timeUp="timeEnd"
          :type="4"
          :theme="2"
          :timeUnit="[$t('nts.天'),$t('nts.时'),$t('nts.分'),$t('nts.秒')]"
        />
      </div>
      <div class="btn-box">
        <div class="btn-title">
          <p>{{$t('nts.拔得头筹  限量抢购')}}</p>
          <p>{{$t('nts.NftList Fund 独家预售')}}</p>
        </div>
        <div class="btn btn-can" @click="$router.push({name: 'Throw'})">{{$t('nts.立 即 参 投')}}</div>
        <div class="btn btn-shuo" @click="showPopup">{{$t('nts.NftList参投告知说明')}}</div>
      </div> 
    </div>
    
    <template v-if="locale == 'zh-cn'">
      <div class="bottom-box">
        <img src="@/assets/images/new/index1.png" alt="">
        <img src="@/assets/images/new/index2.png" alt="">
        <img src="@/assets/images/new/index3.png" alt="">
        <img src="@/assets/images/new/index4.png" alt="">
        <img src="@/assets/images/new/index5.png" alt="">
        <img src="@/assets/images/new/index6.png" alt="">
        <img src="@/assets/images/new/index7.png" alt="">
      </div>
      <van-popup round closeable close-icon="close" v-model:show="showAgreement" :style="{ height: '90%', width: '96%' }" >
        <app-agreement></app-agreement>
      </van-popup>
    </template>
    <template v-if="locale == 'en'">
      <div class="bottom-box">
        <img src="@/assets/images/new/en1.png" alt="">
        <img src="@/assets/images/new/en2.png" alt="">
        <img src="@/assets/images/new/en3.png" alt="">
        <img src="@/assets/images/new/en4.png" alt="">
        <img src="@/assets/images/new/en5.png" alt="">
        <img src="@/assets/images/new/en6.png" alt="">
        <img src="@/assets/images/new/en7.png" alt="">
      </div>
      <van-popup round closeable close-icon="close" v-model:show="showAgreement" :style="{ height: '90%', width: '96%' }" >
        <app-agreement-en></app-agreement-en>
      </van-popup>
    </template>
    
  </div>
</template>
<script>
import IndexCountDown from './IndexCountDown' 
import AppAgreement from '@/components/AppAgreement' 
import AppAgreementEn from '@/components/AppAgreementEn' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import Clipboard from 'clipboard';
import { Toast } from 'vant';
import { getLang, setLang } from "@/utils/i18n";

// import BigNumber from 'bignumber.js';
export default {
  components: {
    // CountDown
    AppAgreement,
    AppAgreementEn,
    IndexCountDown
  },
  setup() {
    var clipboard = new Clipboard('.copy')
    clipboard.on('success', function (e) {
      Toast(t('share.复制成功'))
      e.clearSelection()
    })
    clipboard.on('error', function () {
      Toast(t('share.复制失败，请手动长按复制'))
    }) 
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      time: 1633536000000, // 时间戳
      // time: 1633510710000, // 时间戳
      end: false,
      languageList: [
        { text: '中文', value: 'zh-cn' },
        { text: 'English', value: 'en' }
      ],
      showAgreement: false,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      locale: getLang().toLowerCase() || "en",
      investContract: null,
      abdxContract: null,
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.initData()
      },
      initData: () => {
      },
      timeEnd: ()=>{
        data.end = true
      },
      showPopup: ()=>{
        data.showAgreement = true
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
  .top {
    padding: 10px 0;
    width: 100%;
    overflow: hidden;
    background: #FEE41D;
    text-align: center;
    color: #151515;
    .logo {
      img {
        width: 134px;
        height: auto;
      }
    }
    .title {
      margin-top: 19px;
      font-size: 19px;
      font-weight: bold;
    }
    .btn-box {
      margin-top: 28px;
      .btn-title {
        font-size: 18px;
        font-weight: bold;
        line-height: 1.5;
      }
      .btn-can {
        margin: 18px auto 0;
        width: 197px;
        line-height: 38px;
        text-align: center;
        border-radius: 3px;
        background: linear-gradient(110deg, #7D37BC 0%, #FFB5F4 100%);
        word-spacing: 4px;
        font-size: 19px;
        color: #fff;
        font-weight: bold;
      }
      .btn-shuo {
        margin: 6px auto 0;
        width: 197px;
        line-height: 30px;
        text-align: center;
        border-radius: 3px;
        background: linear-gradient(185deg, #FFFFFF 15%, #F6C9F5 100%);
        word-spacing: 4px;
        font-size: 15px;
        color: #8B45C2;
        font-weight: bold;
      }
    }
  }
  .bottom-box {
    width: 100%;
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

</style>
<style lang="less">
  .index-box {
    .van-dropdown-menu__bar {
      background: #FEE41D;
      justify-content: flex-end;
      height: 38px;
      line-height: 38px;
      // padding: 5px 0;
      box-shadow: none;
    }
    .van-dropdown-menu__item {
      flex: none;
      width: 120px;
      // background: linear-gradient(85deg, #FFFFFF 15%, #F6C9F5 100%);
      background: #FEE41D;
      line-height: 38px;
      border-radius: 3px;
      .van-dropdown-menu__title {
        // color: #111 !important;
      }
    }
    .van-dropdown-item__content {
      width: 120px;
    }
    .van-popup--top {
      left: unset;
      right: 0;
    }
  }
</style>
